<template>
	<view>
		<view class="main">
			<view class="header">
				<view class="header-content" @tap="goLogin">
					<image class="header-image" :src="userInfo.imageurl?userInfo.imageurl:'../../static/head.jpg'"></image>
					<view class="name">
						{{userInfo.realname?userInfo.realname:'未登录'}}
					</view>
					<view class="direction">
						{{userInfo.address}}
					</view>
				</view>
			</view>
			<view class="orders">
				<view class="order-item" @tap="goOrder(1)">
					<image src="../../static/center/nopay.png"></image>
					<view>待付款</view>
				</view>
				<view class="order-item" @tap="goOrder(2)">
					<image src="../../static/center/finshed.png"></image>
					<view>已完成</view>
				</view>
				<view class="order-item" @tap="goOrder(3)">
					<image src="../../static/center/all.png"></image>
					<view>全部订单</view>
				</view>
			</view>
			<view class="tools">
				<view class="title">
					我的工具栏
				</view>
				<view class="items">
					<view class="tools-item" @tap="goAuthentication">
						<image mode="aspectFit" src="../../static/center/cards.png"></image>
						<view>实名认证</view>
					</view>
					<view class="tools-item" @tap="goAdress">
						<image mode="aspectFit" src="../../static/center/recommend.png"></image>
						<view>地址管理</view>
					</view>
					<view class="tools-item" @tap="goBusiness">
						<image mode="aspectFit" src="../../static/center/business.png"></image>
						<view>商务合作</view>
					</view>
					<view class="tools-item" @tap="goSetting">
						<image mode="aspectFit" src="../../static/center/settings.png"></image>
						<view>系统设置</view>
					</view>
				</view>
			</view>
			
			<!-- </view>
			<view class="bottom">
				
			</view> -->
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				userInfo:"",
				products:[]
			}
		},
		onShow(){
			this.products = []
			var userInfo = uni.getStorageSync("userInfoObj")
			this.userInfo = userInfo
			if(userInfo){
				this.loadDetail()
			}
		},
		methods: {
			goBusiness(){
				uni.navigateTo({
					url:"/pages/business/business"
				})
			},
			goAdress(){
				uni.navigateTo({
					url:"/pages/address/address"
				})
			},
			goOrder(status){
				uni.navigateTo({
					url:"/pages/orders/orders?status="+status
				})
			},
			detail(id){
				uni.navigateTo({
					url:"/pages/content/content?id="+id
				})
			},
			loadDetail(){
				var userid = this.userInfo.id;
				var that = this;
				this.$api.loadDetails({
					userid:userid
				}).then(resp=>{
					that.products = resp
				})
			},
			goSetting(){
				uni.navigateTo({
					url:"/pages/setting/setting"
				})
			},
			goAuthentication(){
				uni.navigateTo({
					url:"/pages/authentication/authentication"
				})
			},
			goLogin(){
				if(this.userInfo){
					return;
				}
				uni.navigateTo({
					url:"/pages/login/login"
				})
			}
		}
	}
</script>

